.Root-logo {
  height: 10vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .Root-logo {
    animation: Root-logo-spin infinite 20s linear;
  }
}

@keyframes Root-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
